<template>
  <div class="playpage">
    <div
      class="backgr"
      :style="song && `background-image: url(${song.picUrl})`"
      v-if="song?.picUrl"
    ></div>
    <div
      class="backgr"
      :style="song && `background-image: url(${song?.al?.picUrl})`"
      v-else
    ></div>
    <div class="header">
      <div class="close">
        <span class="iconfont close" @click="$parent.showPlayPage = false"
          >&#xe69b;</span
        >
      </div>
      <div class="song-text">
        <h3 class="name">{{ song?.name }}</h3>
        <p v-if="song?.song">{{ song?.song?.album?.artists["0"]?.name }}</p>
        <p v-else>{{ song?.ar["0"]?.name }}</p>
      </div>
      <div class="share">
        <span class="iconfont share">&#xebab;</span>
      </div>
    </div>
    <section class="songcar" v-if="showLyric" @click="showLyric = false">
      <div class="msong-disc">
        <div class="msong-turn">
          <div class="song-rollwarp">
            <div class="song-img" :class="{ playing: playing }">
              <img
                :src="`${song?.picUrl}?imageView&thumbnail=360y360`"
                alt=""
                :class="{ playing: playing }"
                v-if="song?.picUrl"
              />
              <img
                :src="`${song?.al?.picUrl}?imageView&thumbnail=360y360`"
                alt=""
                :class="{ playing: playing }"
                v-else
              />
            </div>
          </div>
          <div class="song-lgour">
            <div class="song-light"></div>
          </div>
        </div>
        <div class="needle">
          <div class="needle-ab" :class="{ playing: playing }"></div>
        </div>
      </div>
    </section>
    <section class="musiclyric" v-else @click="showLyric = true">
      <div class="lyriccar">
        <MusicLyric
          :song="song"
          :musiclyric="musiclyric"
          :currentTime="currentTime"
          :audio="audio"
          v-if="musiclyric"
        />
      </div>
      <!---->
    </section>
    <footer class="footer">
      <div class="music-play">
        <ProgressBar
          :duration="duration"
          :currentTime="currentTime"
          @update-time="$emit('update-time', $event)"
        />
        <div class="music-btn">
          <div class="music-model" @click="$emit('change-model')">
            <span class="iconfont current" v-show="model === '顺'"
              >&#xe69d;</span
            >
            <span class="iconfont random" v-show="model === '随'"
              >&#xe6a7;</span
            >
            <span class="iconfont single" v-show="model === '单'"
              >&#xe6a2;</span
            >
          </div>
          <div class="music-check">
            <span class="iconfont prv" @click="$emit('change-song', 'prev')"
              >&#xe6a1;</span
            >
            <div class="play-music">
              <span
                class="iconfont pause"
                v-if="playing"
                @click.stop="$emit('music-pause')"
                >&#xe69f;</span
              >
              <span
                class="iconfont play"
                v-else
                @click.stop="$emit('music-play')"
                >&#xe6a5;</span
              >
            </div>
            <span class="iconfont next" @click="$emit('change-song', 'next')"
              >&#xe69e;</span
            >
          </div>
          <div class="list">
            <span
              class="iconfont musilist"
              @click="$emit('toggle-showPlayList')"
              >&#xea71;</span
            >
          </div>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import MusicLyric from "@/components/MusicLyric.vue";
import ProgressBar from "@/components/ProgressBar.vue";
export default {
  components: {
    MusicLyric,
    ProgressBar,
  },
  props: {
    song: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    model: String,
    audio: HTMLAudioElement,
  },

  data: function () {
    return {
      showLyric: true,
      draging: false,
      // tmpCurrentTime: this.currentTime,
      newwidth: 0,
      newCurrentTime: "00:00",
      totaltime:
        String(Math.floor(this.duration / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.duration % 60)).padStart(2, "0"),

      musiclyric: null,
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/lyric?", {
        params: { id: this.song?.id },
      })
      .then((res) => {
        this.musiclyric = res.data;
      });
  },
  watch: {
    // currentTime: function (n) {
    //   // console.log(n);
    //   if (!this.draging) {
    //     this.tmpCurrentTime = n;
    //   }
    //   this.newwidth = (n / this.duration) * 100;
    //   this.newCurrentTime =
    //     String(Math.floor(n / 60)).padStart(2, "0") +
    //     ":" +
    //     String(Math.floor(n % 60)).padStart(2, "0");
    //   // console.log(this.newwidth);
    // },
    song: function (n) {
      // console.log(n);
      this.axios
        .get("https://apis.netstart.cn/music/lyric?", {
          params: { id: n.id },
        })
        .then((res) => {
          this.musiclyric = res.data;
        });
    },
  },
};
</script>

<style lang="less">
