          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          z-index: 2;
          background: url("../assets/dci.png") no-repeat;
          background-size: contain;
        }
        .song-rollwarp {
          position: absolute;
          left: 50%;
          top: 50%;
          z-index: 1;
          width: 184px;
          height: 184px;
          margin: -92px 0 0 -92px;

          .song-img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            overflow: hidden;
            background: url("../assets/disc_default.png") no-repeat;
            background-size: contain;
            animation: rotate 20s linear infinite;
            animation-play-state: paused;
            &.playing {
              animation-play-state: running;
            }
            img {
              // width: 200rem;
              display: block;
              width: 100%;
              border-radius: 50%;
              box-shadow: 0 0 10px 2px #fcfdfe;
              animation: rotate 20s linear infinite;
              animation-play-state: paused;
              &.playing {
                animation-play-state: running;
              }
            }
          }
        }
      }
      .song-lgour {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 3;
        .song-light {
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          z-index: 3;
          background: url("../assets/ddci.png") no-repeat;
          background-size: contain;
        }
      }
      .needle {
        position: absolute;
        z-index: 5;
        width: 73px;
        height: 118px;
        top: -50px;
        left: 112px;
        .needle-ab {
          transform-origin: -6rem 7rem;
          transform: rotate(331deg);
          width: 100%;
          height: 100%;
          background: url("../assets/needle-ab.png") no-repeat;
          background-size: contain;
          &.playing {
            transform-origin: -90rem 10rem;
            transform: rotate(-5deg);
          }
        }
      }
    }
  }
  .musiclyric {
    z-index: 999;
    width: 100%;
    height: 25%;
  }
  .footer {
    z-index: 999;
    .music-play {
      padding: 10rem;
      padding-bottom: 15rem;
      box-sizing: border-box;

      // .music-controls {
      //   display: flex;
