      .music-btn {
        display: flex;
        justify-content: space-around;
        align-items: center;
        .music-model {
          width: 44rem;
          height: 44rem;
          display: flex;
          position: relative;
          span {
            display: inline-block;
            font-size: 42em;
            color: #fff;
          }
          span.current {
            position: absolute;
          }
          span.random {
            position: absolute;
          }
          span.single {
            position: absolute;
          }
        }
        .music-check {
          display: flex;
          align-items: center;
          span {
            font-size: 44rem;
            color: #fff;
          }
          .play-music {
            position: relative;
            width: 60rem;
