<template>
  <a
    class="music-car"
    @click.prevent="$router.push({ path: '/songlist', query: { id: item.id } })"
  >
    <div class="rem-img">
      <img
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=370x0`"
        alt=""
      />
      <span>{{ listMusic }}</span>
    </div>
    <p>{{ item.name }}</p>
  </a>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  computed: {
    listMusic: function () {
      return this.item.playCount > 100000000
        ? Math.round(this.item.playCount / 10000000) / 10 + "亿"
        : this.item.playCount > 100000
        ? Math.round(this.item.playCount / 1000) / 10 + "万"
        : this.item.playCount;
    },
  },
};
</script>

<style lang="less">
a.music-car {
  text-decoration: none;
  width: 30%;
  // flex-grow: 1;
  display: block;
  margin-bottom: 16rem;
  color: rgb(209, 203, 203);
  background-color: #111111;
  border-radius: 5rem;

  // &:nth-child(3n + 2) {
  //   margin: 0 5rem;
  // }
  .rem-img {
    position: relative;
    img {
      display: block;
      font-size: 0;
      border-radius: 5rem;
    }
    span {
      position: absolute;
      right: 5rem;
      top: 2rem;
      z-index: 3;
      padding-left: 13rem;
      color: rgb(252, 252, 252);
      font-size: 12rem;
      background-position: 0;
      background-repeat: no-repeat;
      background-size: 11rem 10rem;
      text-shadow: 1rem 0 0 rgb(0 0 0 / 15%);
      // background-image: url("../assets/music.svg");
    }
  }
  p {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 6rem 2rem 0 6rem;
    min-height: 30rem;
    line-height: 1.2;
    font-size: 13rem;
  }
}
</style>>
