<template>
  <div class="songly" v-if="musiclyric">
    <ul ref="ul" class="lry" v-if="texts">
      <li ref="li" class="text" v-for="(text,i) in texts" :key="i">
        {{ text?.text }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
    currentTime: Number,
    musiclyric: Object,
  },
  data() {
    return {
      // musiclyric: null,
      texts: null,
      index: 0,

      offsetHeight: "",
      ontimeupdates: "",
      // texttt:"",
    };
  },
  // created() {
  //   this.axios
  //     .get("https://apis.netstart.cn/music/lyric?", {
  //       params: { id: this.song?.id },
  //     })
  //     .then((res) => {
  //       this.musiclyric = res.data;
  //     });
  // },
  methods: {
    songlyric: function () {
      var lyric = this.musiclyric?.lrc?.lyric;
      let arr = lyric.split("\n");
      //   console.log(arr);
      let parsed = arr
        .filter((o) => o)
        .map((o) => {
          let { groups } = o.match(
            /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
          );
          return {
            time: groups.mm * 60 + groups.ss * 1,
            text: groups.text.trim(),
          };
        });
      //   console.log(parsed);
      this.texts = parsed;
      //   console.log(this.texts);
    },

    findCurrentLyric: function (parsed, currentTime) {
      var copy = [...parsed].reverse();
      var index = copy.findIndex((o) => o.time < currentTime);
      return {
        index: copy.length - 1 - index,
        text: copy[index]?.text,
      };
    },
    ontimeupdate: function () {
      this.ontimeupdates = this.findCurrentLyric(this.texts, this.currentTime);
    },
    move: function () {
      this.$refs.li.forEach((element, index) => {
        // console.log(index);
        if (index ===  this.ontimeupdates.index) {
          element.style.color = "red";
        } else {
          element.style.color = "";
        }
      });
      this.$refs.ul.style.transform = `translateY(${ this.ontimeupdates.index* -30 + 150}rem)`;
    },
  },
  mounted() {
    if (this.musiclyric) {
      this.songlyric();
    }
  },
  watch: {
    currentTime: function ( ) {
      //   console.log(n);
      // var o = this.findCurrentLyric(this.texts, n);
       this.ontimeupdate();
      this.move();
    },
    musiclyric: function (n) {
      if (n) {
        this.songlyric();
      }
    },
    // song:function (n) {
    //   console.log(n);
    //   this.axios
    //   .get("https://apis.netstart.cn/music/lyric?", {
    //     params: { id:n.id},
    //   })
    //   .then((res) => {
    //     this.musiclyric = res.data;
    //   });
    // }
  },
};
</script>

<style lang="less">
.songly {
  width: 100%;
  height: 70%;
  // background-color: rgba(175, 175, 175, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  // z-index: 999;
  //   display: flex;
  //   flex-direction: column;
  //   justify-content: space-between;
  z-index: 99;
  text-align: center;
  overflow: hidden;
  ul.lry {
    // height: 100%;
    width: 80%;
    margin: 0 auto;
    margin-top: 30%;
    li.text {
      color: #fff;
      font-size: 14rem;
      padding: 5rem 0;
    }
  }
}
