<template>
  <div class="playlist" @click.stop="$emit('toggle-showPlayList')">
    <div class="listpage">
      <div class="carr">
        <h3 class="tit">播放列表</h3>
        <ul class="list">
          <li
            class="song-list"
            v-for="song in songlist"
            :key="song.id"
            @click="$emit('music-play', song)"
            :style="{ color: song?.id === songId ? '#03c862' : '' }"
          >
            <!-- {{ song.name }} -->
            <div class="carr-cred">
              <img :src="song?.picUrl" alt="" v-if="song?.picUrl" />
              <img :src="song?.al?.picUrl" alt="" v-else />
            </div>
            <div class="hotsong-item">
              <div class="hotsg">
                <div class="f-thid">{{ song.name }}</div>
                <div
                  class="f-info"
                  :style="{ color: song?.id === songId ? '#03c862' : '' }"
                >
                  <!-- <i class="sgihot" v-if="hotsong.sq !== null"></i> -->
                  {{ song?.name }} ·
                  <span v-if="song?.song">{{
                    song?.song?.album?.artists["0"]?.name
                  }}</span>
                  <span v-else>{{ song?.ar["0"]?.name }}</span>
                </div>
              </div>
              <div class="hotauto" v-if="song.id !== songId">
                <span class="hots"></span>
              </div>
              <div v-else class="hotauto active" :class="{ playing: playing }">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    songlist: [Array, Object],
    songId: [Number, String],
    playing: Boolean,
  },
};
</script>

<style lang="less">
.playlist {
  width: 100%;
  height: 100vh;

  position: fixed;
  top: 0;
  // bottom: 60rem;
  // left: 0;
  // z-index: 999;
  padding: 15rem;
  color: rgb(208, 204, 204);
  display: flex;
  padding-left: 15rem;
  background-color: #2929292d;
  z-index: 99;

  .listpage {
    width: 100%;
    height: 60vh;

    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 999;
    padding: 15rem;
    overflow: auto;
    border-radius: 15rem;
    padding: 15rem;
    background-color: #292929;
    min-height: calc(60vh - 60rem);
    .carr {
      h3.tit {
        font-size: 16rem;
        font-weight: 600;
        padding-bottom: 10rem;
      }
      ul.list {
        // margin-bottom: 60rem;
        li.song-list {
          font-size: 14rem;
          padding: 5rem 0;
          display: flex;
          .carr-cred {
            display: flex;
            align-items: center;
            img {
              display: block;
              width: 40rem;
              height: 40rem;
            }
          }
          .hotsong-item {
            -webkit-box-flex: 1;
            flex: 1 1 auto;
            display: flex;
            position: relative;
            padding-left: 10rem;
            &:after {
              position: absolute;
              // z-index: 2;
              content: "";
              top: 0;
              left: 0;
              pointer-events: none;
              box-sizing: border-box;
              width: 200%;
              height: 200%;
              transform-origin: top left;
              border: 0 solid rgba(255, 255, 255, 0.1);
              border-bottom-width: 1rem;
              transform: scale(0.5);
            }
            .hotsg {
              -webkit-box-flex: 1;
              flex: 1 1 auto;
              padding: 6rem 0;
              width: 0;
              .f-thid {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                word-break: normal;
                font-size: 17rem;
              }
              .f-info {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                word-break: normal;
                font-size: 12rem;
                color: #888;
                i.sgihot {
                  display: inline-block;
                  width: 12rem;
                  height: 8rem;
                  margin-right: 4rem;
                  background: url("../assets/index_icon_2x.png") no-repeat;
                  background-size: 166rem 97rem;
                }
              }
            }
            @keyframes playingrunning {
              from {
                transform: rotateX(15deg);
              }
              to {
                transform: rotateX(60deg);
              }
            }
            .hotauto {
              display: flex;
              -webkit-box-align: center;
              align-items: center;
              padding: 0 10rem;
              position: relative;
              span.hots {
                display: inline-block;
                width: 22rem;
                height: 22rem;
                background: url("../assets/index_icon_2x.png") no-repeat;
                background-position: -24rem 0;
                background-size: 166rem 97rem;
              }
              &.active {
                display: flex;
                justify-content: space-around;
                width: 22rem;
                height: 22rem;
                position: absolute;
                right: 10rem;
                top: 50%;
                padding: 0;
                transform: translateY(-50%);
                span.u-hm {
                  background-image: none;
                }
                span {
                  width: 11%;
                  height: 100%;
                  background: #03c862;
                  animation: playingrunning 0.8s infinite alternate;
                  animation-play-state: paused;
                  transform-origin: bottom;
                  &:nth-child(1) {
                    animation-delay: 0s;
                  }
                  &:nth-child(2) {
                    animation-delay: -0.2s;
                  }
                  &:nth-child(3) {
                    animation-delay: -0.4s;
                  }
                  &:nth-child(4) {
                    animation-delay: -0.6s;
                  }
                }
                &.playing {
                  span {
                    animation-play-state: running;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>>
