<template>
  <div class="comment">
    <div class="cmt-head">
      <a href="#" class="userphoto">
        <img :src="songlistcomment?.user?.avatarUrl" alt="" />
        <span class="ava-icon" v-if="songlistcomment?.user?.experts !== null">
        </span>
      </a>
    </div>
    <div class="cmd-warp">
      <div class="cmd-header">
        <div class="cmt-meta">
          <span class="cmt-user">
            <a href="#" class="nickname">
              {{ songlistcomment?.user?.nickname }}
            </a>
            <!-- <img
              :src="songlistcomment?.user?.vipRights?.associator?.iconUrl"
              alt=""
              v-if="songlistcomment?.user?.vipRights !== null"
            /> -->
          </span>
          <div class="cmt-time">
            <span> {{ updateTime }}</span>
          </div>
        </div>
        <div class="cmt-like">
          <span class="likearea">
            <span class="count">{{ songlistcomment?.likedCount }}</span>
            <i class="likeicn">
              <svg
                class="u-svg u-svg-unzancmt"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 28 28"
              >
                <path
                  fill="#999"
                  d="m25.857 14.752c-.015.059-1.506 5.867-2.932 8.813-1.162 2.402-3 2.436-3.099 2.436h-12.826v-13c3 0 5.728-4 5.728-7.275 0-3.725 1.433-3.725 2.142-3.725 1.327 0 1.978 1.345 1.978 4 0 2.872-.832 4.525-.839 4.537-.161.31-.155.682.027.981.181.299.5.482.849.482h6.942c.922 0 1.551.215 1.866.64.467.626.286 1.705.164 2.112m-23.857 10.248v-10c0-1.795.659-1.981.855-2h2.145v13h-2.173c-.829 0-.827-.648-.827-1m25.309-13.54c-.713-.969-1.886-1.46-3.482-1.46h-5.519c.26-.932.519-2.285.519-4 0-5.221-2.507-6-4-6-1.909 0-4.185.993-4.185 5.725 0 2.206-1.923 5.275-3.815 5.275h-4-.011c-1.034.011-2.816.862-2.816 4v10.02c0 1.198.675 2.979 2.827 2.979h16.971.035c.364 0 3.224-.113 4.894-3.564 1.514-3.127 3.01-8.942 3.056-9.14.071-.23.664-2.289-.474-3.836"
                ></path>
              </svg>
            </i>
          </span>
        </div>
      </div>
      <div class="cmt-content">
        <span class="cmt-text" v-if="songlistcomment?.beReplied.length !== 0">
          回复
          <a class="att" href="#"
            >@{{ songlistcomment?.beReplied[0]?.user?.nickname }}</a
          >:
        </span>
        <span class="cmt-text">{{ songlistcomment?.content }}</span>
      </div>
      <div class="cmt-relied" v-if="songlistcomment?.beReplied.length !== 0">
        <span class="relied-user"
          >{{ songlistcomment?.beReplied[0]?.user?.nickname }}
        </span>
        :
        <span class="relied-cnt">{{
          songlistcomment?.beReplied[0]?.content
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    songlistcomment: Object,
  },
  data: function () {
    return {
      time: this.songlistcomment?.timeStr,
      // nwetime:"",
    };
  },
  computed: {
    updateTime: function () {
      return (
        this.time.slice(0, 4) +
        "年" +
        this.time.slice(5, 7) +
        "月" +
        this.time.slice(8) +
        "日"
      );
    },
  },
  watch: {
    time: function (n, o) {
      console.log(n, o);
      n = o.slice(0, 4) + "年" + o.slice(5, 7) + "月" + o.slice(8) + "日";
    },
  },
};
</script>
<style lang="less">
.comment {
  padding-top: 10px;
  width: 100%;

  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  background-color: #111;

  .cmt-head {
    height: 35px;
    margin: 0 10px;
    -webkit-box-flex: 0;
    flex: none;
    a.userphoto {
      position: relative;
      display: block;
      // color: #507daf;
      text-decoration: none;
      img {
        width: 30rem;
        height: 30rem;
        display: block;
        border-radius: 50%;
      }
      span.ava-icon {
        position: absolute;
        right: -5px;
        bottom: 0;
        width: 12px;
        height: 12px;
        background-image: url("../assets/usericn_2x.png");
        background-repeat: no-repeat;
        background-size: 75px auto;
        background-position: -40px 0;
      }
    }
  }
  .cmd-warp {
    padding-right: 10px;
    padding-bottom: 11px;
    -webkit-box-flex: 1;
    flex: auto;
    width: 0;
    position: relative;
    &:after {
      position: absolute;
      z-index: 2;
      content: "";
      top: 0;
      left: 0;
      pointer-events: none;
      box-sizing: border-box;
      width: 200%;
      height: 200%;
      transform-origin: top left;
      border: 0 solid rgba(0, 0, 0, 0.1);
      border-bottom-width: 1rem;
      transform: scale(0.5);
    }
    .cmd-header {
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      flex-direction: row;
      .cmt-meta {
        -webkit-box-flex: 1;
        flex: auto;
        width: 0;
        font-size: 0;
        span.cmt-user {
          max-width: 100%;
          display: inline-flex;
          -webkit-box-align: center;
          align-items: center;
          a.nickname {
            font-size: 14px;
            color: rgb(212, 210, 210);
            line-height: 20px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            -webkit-box-flex: 1;
            flex: auto;
            text-decoration: none;
          }
          img {
            width: 27rem;
            -webkit-box-flex: 0;
            flex: 0 0 auto;
            display: inline-block;
            margin: 0 4px;
            border: 0;
          }
        }
        .cmt-time {
          font-size: 9px;
          color:rgb(212, 210, 210);
        }
      }
      .cmt-like {
        width: 65px;
        height: 22px;
        line-height: 22px;
        font-size: 11px;
        color: rgb(212, 210, 210);
        -webkit-box-flex: 0;
        flex: none;
        text-align: right;
        span.likearea {
          display: inline-block;
          min-width: 30px;
          padding-left: 5px;
          span.count {
            vertical-align: middle;
          }
          i.likeicn {
            display: inline-block;
            width: 14px;
            height: 14px;
            margin-left: 6px;
            line-height: 0;
            vertical-align: middle;
            cursor: pointer;
            svg {
              display: inline-block;
              vertical-align: middle;
              background-position: 0 0;
              background-size: contain;
              background-repeat: no-repeat;
            }
          }
        }
      }
    }
    .cmt-content {
      color:rgb(212, 210, 210);
      font-size: 15px;
      line-height: 22px;
      margin-top: 5px;
      vertical-align: middle;

      span.cmt-text {
        vertical-align: middle;
        a.att {
          text-decoration: none;
          color: #507daf;
        }
      }
    }

    .cmt-relied {
      margin: 5px 0;
      padding: 10px;
      color: #888;
      font-size: 14px;
      line-height: 21px;
      position: relative;
      &:after {
        position: absolute;
        // z-index: 2;
        content: "";
        top: 0;
        left: 0;
        pointer-events: none;
        box-sizing: border-box;
        width: 200%;
        height: 200%;
        transform-origin: top left;
        border: 0 solid rgba(0, 0, 0, 0.1);
        border-width: 1px;
        transform: scale(0.5);
      }

      span.relied-user {
        vertical-align: middle;
      }
      span.relied-cnt {
        vertical-align: middle;
      }
    }
  }
}
</style>