        </div>
        <router-link to="/search">
          <div class="tabtext" @click="serch = false">
            <span class="iconfont serch">&#xe6e2;</span>
            <!-- <SearchView/> -->
          </div>
        </router-link>
      </nav>
    </div>

    <router-view
      @music-play="song = $event"
      @music-play-list="songlist = $event"
      :songId="song?.id"
      :playing="playing"
      @srech-back="
        serch = true;
        back();
      "
      :style="{ marginBottom: song ? '60rem' : '0' }"
    />
    <audio
      :src="songSrc"
      autoplay
      ref="audio"
      @play="playing = true"
      @pause="playing = false"
      @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"
    ></audio>
    <div class="musicplay" v-if="song" @click="showPlayList = false">
      <MusicPlay
        :song="song"
        :playing="playing"
        @music-pause="$refs.audio.pause()"
        @music-play="$refs.audio.play()"
        :duration="duration"
        :currentTime="currentTime"
        @click.native="showPlayPage = true"
        @toggle-showPlayList="showPlayList = !showPlayList"
      />
    </div>
    <div class="showPlayPage" v-if="song && showPlayPage">
      <PlayDetails
        :song="song"
        :playing="playing"
        @music-pause="$refs.audio.pause()"
        @music-play="$refs.audio.play()"
        :duration="duration"
        :currentTime="currentTime"
        @update-time="$refs.audio.currentTime = $event"
        @toggle-showPlayList="showPlayList = !showPlayList"
        :model="model"
        @change-model="changeModel"
        @change-song="changeSong"
        :audio="$refs.audio"
      />
    </div>
    <div class="showplaylist" v-show="showPlayList">
      <PlayList
        :songlist="songlist"
        :songId="song?.id"
        @music-play="song = $event"
        @toggle-showPlayList="showPlayList = !showPlayList"
        :playing="playing"
      />
    </div>
  </div>
</template>
<script>
import MusicPlay from "@/components/MusicPlay.vue";
import PlayDetails from "@/components/PlayDetails.vue";
import PlayList from "@/components/PlayList.vue";
// import SearchView from "@/views/SearchView.vue";

export default {
  components: {
    MusicPlay,
    PlayDetails,
    PlayList,
    // SearchView,
  },
  data() {
    return {
      song: JSON.parse(localStorage.getItem("song")),
      playing: false,
      duration: 0,
      currentTime: Number(localStorage.getItem("currentTime")) || 0,
      showPlayPage: false,
      showPlayList: false,
      songlist: JSON.parse(localStorage.getItem("songlist")) || [],
      models: ["顺", "随", "单"],
      model: JSON.parse(localStorage.getItem("model")) || "顺",
      serch: true,
      // bar: false,
      // songId:null,
    };
  },
  mounted() {
    this.$refs.audio.currentTime = this.currentTime;
  },
  methods: {
    changeModel: function () {
      // console.log(model);
      var index = this.models.findIndex((o) => o === this.model);

      this.model =
        index === this.models.length - 1
          ? this.models[0]
          : this.models[index + 1];
    },
    changeSong: function (direction) {
      // console.log(direction);
      switch (this.model) {
        case "顺":
          var currentSongIndex = this.songlist.findIndex(
            (o) => o.id === this.song.id
          );
          if (direction === "next") {
            this.song =
              currentSongIndex === this.songlist.length - 1
                ? this.songlist[0]
                : this.songlist[currentSongIndex + 1];
          } else {
            this.song =
              currentSongIndex === 0
                ? this.songlist[this.songlist.length - 1]
                : this.songlist[currentSongIndex - 1];
          }
          break;
        case "随":
          var daixuan = this.songlist.filter((o) => o.id !== this.song.id);
          this.song = daixuan[Math.floor(Math.random() * daixuan.length)];
          break;
        case "单":
          this.$refs.audio.currentTime = 0;
          this.$refs.audio.play();
          break;
      }
    },
    back() {
      this.$router.replace("/");
    },
  },
  // created() {
  //   if(this.$router.path !== '/'){
  //     this.$router.replace('/')
  //   }
  // },
  computed: {
    songSrc: function () {
      return this.song
        ? `https://music.163.com/song/media/outer/url?id=${this.song.id}.mp3`
        : null;
    },
  },
  watch: {
    currentTime: function (time) {
      if (time == this.duration) {
        this.changeSong(this.model);
      }
      localStorage.setItem("currentTime", JSON.stringify(time));
    },
    song: function (song) {
      // console.log(song);
      localStorage.setItem("song", JSON.stringify(song));
    },
    songlist: function (songlist) {
      // console.log(song);
      localStorage.setItem("songlist", JSON.stringify(songlist));
    },
    playing: function (playing) {
      // console.log(song);
      localStorage.setItem("playing", JSON.stringify(playing));
    },
    model: function (model) {
      // console.log(song);
      localStorage.setItem("model", JSON.stringify(model));
    },
  },
};
</script>

