<template>
  <div class="songplay">
    <div class="song-text">
      <div class="songimg">
        <img
          :src="song?.picUrl"
          alt=""
          :class="{ playing: playing }"
          v-if="song?.picUrl"
        />
        <img
          :src="song?.al?.picUrl"
          alt=""
          :class="{ playing: playing }"
          v-else
        />
      </div>
      <h3>
        {{ song?.name }} .
        <span v-if="song?.song">{{
          song?.song?.album?.artists["0"]?.name
        }}</span>
        <span v-else>{{ song?.ar["0"]?.name }}</span>
      </h3>
    </div>
    <div class="music-colr">
      <div class="musi-play">
        <div class="songplay">
          <span
            class="iconfont pasue"
            v-if="playing"
            title="暂停"
            @click.stop="$emit('music-pause')"
            >&#xea6d;</span
          >
          <span
            class="iconfont play"
            v-else
            title="播放"
            @click.stop="$emit('music-play')"
            >&#xea6e;</span
          >
          <!-- <canvas id="canvasback" ref="canvasback" width="60" height="60"></canvas> -->
          <canvas id="canvas" ref="canvas" width="60" height="60"></canvas>
        </div>
      </div>
      <div class="musilist">
        <div class="list">
          <span
            class="iconfont musilist"
            @click.stop="$emit('toggle-showPlayList')"
            >&#xea71;</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // data(){
  //     return{
  //         canvas:this.$refs.canvas,
  //     }
  // },

  props: {
    song: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    // bar: Boolean,
  },
  computed: {},
  // mounted() {
  //   this.progressbar();
  // },
  methods: {
    // progressbar() {
    //   var canvas = this.$refs.canvas;
    //   //   canvas.width = 40;
    //   //   canvas.height = 40;
    //   var bar = canvas.getContext("2d");
    //   // bar.save();
    //   // bar.clearRect(0, 0, 60, 60);
    //   bar.lineCap = "round";
    //   bar.strokeStyle = "#e5f1fa";
    //   bar.lineWidth = 2;
    //   bar.beginPath();
    //   bar.arc(20, 20, 16, 0, 2 * Math.PI);
    //   bar.stroke();
    //   // bar.closePath();
    //   // bar.restore();
    // },
    upadebar(val) {
      var canvas = this.$refs.canvas;
      //   canvas.width = 40;
      //   canvas.height = 40;
      var bar = canvas.getContext("2d");
      // bar.save();
      bar.clearRect(0, 0, 60, 60);
      bar.lineCap = "round";
      bar.strokeStyle = "#2ba0fb";
      bar.lineWidth = 2;
      bar.beginPath();
      bar.arc(20, 20, 16, -Math.PI / 2, -Math.PI / 2 + 2 * val * Math.PI);
      bar.stroke();
      // bar.closePath();
      // bar.restore();
    },
  },
  watch: {
    currentTime(n) {
      //   console.log(n);
      // console.log(n / this.duration);
      // var canvas = this.$refs.canvas;
      // var bar = canvas.getContext("2d");
      // bar.clearRect(0, 0, 60, 60);
      // this.progressbar();
      this.upadebar((n / this.duration) * 1);
    },
  },
};
</script>

<style lang="less">
