        align-items: center;
        justify-content: center;
        text-align: center;
        span.pasue {
          display: inline-block;
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
          font-size: 20rem;
        }
        span.play {
          display: inline-block;
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;

          font-size: 20rem;
        }
      }
      // canvas#canvasback {
      //   width: 60rem;
