      //   position: absolute;
      //   z-index: -2;
      //   // top: calc(50% + 3rem);
      //   left: calc(50% - 21rem);
      //   top: calc(50% + 2rem);
      //   // top:3rem;
      //   // left: 50%;
      // }
      canvas#canvas {
        // display: block;
        // width: 60rem;
        // height: 60rem;
        position: absolute;
        z-index: -1;
        top: 50%;
        left: calc(50% - 21rem);
        // top: calc(50% - 2rem);
        // top:3rem;
        // left: 50%;
      }
    }
    .musilist {
      width: 44rem;
      height: 44rem;
      display: flex;
      align-items: center;
      line-height: 44rem;
     
      margin: 0 5px;
      // margin-left: 0;

      .list {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        span.musilist {
          font-size: 22rem;
