<template>
  <div class="music-controls">
    <span class="star">{{ newCurrentTime }}</span>
    <div class="contro">
      <label>
        <input
          class="bar"
          type="range"
          name="buffer"
          id=""
          min="0"
          :max="duration"
          :value="tmpCurrentTime"
          @input="
            draging = true;
            updateBar($event.target.value, duration);
          "
          @change="
            draging = false;
            $emit('update-time', $event.target.value);
          "
        />
        <div class="buffer">
          <span class="bar" ref="span"></span>
        </div>
        <!--:style="{ width: newwidth + '%' }" -->
      </label>
    </div>
    <span class="end">{{ totaltime }}</span>
  </div>
</template>

<script>
export default {
  props: {
    duration: Number,
    currentTime: Number,
  },
  data: function () {
    return {
      //   showLyric: true,
      draging: false,
      tmpCurrentTime: this.currentTime,
      newwidth: 0,
      newCurrentTime: "00:00",
      updatetime: 0,
      totaltime:
        String(Math.floor(this.duration / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.duration % 60)).padStart(2, "0"),
    };
  },
  methods: {
    updateBar(timing, timed) {
      let barlength = Math.ceil((timing / timed) * 100) + "%";
      this.$refs.span.style.width = barlength;
    },
  },
  mounted() {
    let barlength = Math.ceil((this.currentTime / this.duration) * 100) + "%";
    this.$refs.span.style.width = barlength;
  },
  watch: {
    currentTime: function (n) {
      // console.log(n);
      if (!this.draging) {
        this.tmpCurrentTime = n;
        this.updateBar(this.tmpCurrentTime, this.duration);
      }
      // this.newwidth = (n / this.duration) * 100;
      this.newCurrentTime =
        String(Math.floor(n / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(n % 60)).padStart(2, "0");
      // console.log(this.newwidth);
    },
    updatetime(updatetime) {
      this.tmpCurrentTime = updatetime;
    },
  },
};
</script>

<style lang="less">
.music-controls {
  display: flex;
  align-items: center;
  padding: 0 3rem;
  z-index: 9;
  .contro {
    padding: 0 5rem;
    box-sizing: border-box;
    label {
      position: relative;
      display: block;
      input.bar {
        width: 280rem;
        margin: 0;
        height: 5rem;
        display: block;
        opacity: 0;
        z-index: 9;
      }
      .buffer {
        width: 280px;
        height: 5px;
        background-color: rgb(255, 255, 255);
        display: flex;
        align-items: center;
        border-radius: 10px;
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        span.bar {
          height: 5rem;
          display: block;
          width: 100rem;
          border-radius: 5px;
          background-color: rgb(103, 167, 226);
        }
      }
    }
  }
  span.star {
    color: #fff;
  }
  span.end {
    color: #fff;
  }
}
</style>>
