<template>
  <div class="u-height">
    <section class="loader" v-if="loading">
      <div class="loading">
        <img src="../assets/loading.gif" alt="" />
      </div>
    </section>
    <div class="root" v-if="!loading">
      <section class="pylst">
        <span class="iconfont back" @click="$emit('srech-back')">&#xe6db;</span>
        <div
          class="pylst_header"
          :style="songlist && `background-image: url(${songlist.coverImgUrl})`"
        >
          <div class="plhead-warp">
            <div class="plhead-fl">
              <img class="u-img" :src="songlist?.coverImgUrl" alt="" />
              <span class="lsth-icon">歌单</span>
              <span class="u-earp">{{ listMusic }}</span>
            </div>
            <div class="plhead-fr">
              <h2 class="listhd">{{ songlist?.name }}</h2>
              <div class="listhd-auth">
                <a class="listhd-link" href="">
                  <div class="u-avatar">
                    <img
                      class="auth-img"
                      :src="songlist?.creator?.avatarUrl"
                      alt=""
                    />
                    <span class="ava-icon"></span>
                  </div>
                  {{ songlist?.creator?.nickname }}
                </a>
              </div>
            </div>
          </div>
        </div>
      </section>
      <div class="songlist">
        <h3 class="smtitle">歌曲列表</h3>
        <SongListMusic
          class="sglist"
          v-for="(song, i) in songlist?.tracks"
          :key="song.id"
          :i="i"
          :song="song"
          :songId="songId"
          @music-play="$emit('music-play', $event)"
          @music-play-list="$emit('music-play-list', songlist?.tracks)"
          :playing="playing"
        />
      </div>

      <div class="n-newcomm">
        <h3 class="smtitle">精彩评论</h3>
        <SongListComment
          class="sglist"
          v-for="(songlistcomment, i) in songlistcomments"
          :key="i"
          :songlistcomment="songlistcomment"
        />
      </div>
    </div>
  </div>
</template>

<script>
import SongListMusic from "@/components/SongListMusic.vue";
import SongListComment from "@/components/SongListComment.vue";

export default {
  components: {
    SongListMusic,
    SongListComment,
  },
  props: {
    songId: [String, Number],
    playing: Boolean,
  },
  data: function () {
    return {
      songlist: null,
      loading: false,
      songlistcomments: null,
    };
  },
  computed: {
    listMusic: function () {
      return this.songlist?.playCount > 100000000
        ? Math.round(this.songlist?.playCount / 10000000) / 10 + "亿"
        : this.songlist?.playCount > 100000
        ? Math.round(this.songlist?.playCount / 1000) / 10 + "万"
        : this.songlist?.playCount;
    },
  },
  methods: {
    getSongList: function (id) {
      this.loading = true;
      Promise.all([
        this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
          params: { id },
        }),
        this.axios.get("https://apis.netstart.cn/music/comment/playlist", {
          params: { id },
        }),
      ]).then(([list, comm]) => {
        this.songlist = list.data.playlist;
        // console.log(comm);
        this.songlistcomments = comm.data.hotComments;
        this.loading = false;
      });
    },
  },
  created: function () {
    this.getSongList(this.$route.query.id);
  },
  beforeRouteUpdate(to, from, next) {
    this.getSongList(to.query.id);

    next();
  },
};
</script>

<style lang="less">
h3.smtitle {
  height: 23px;
  line-height: 23px;
  padding: 0 10px;
  font-size: 12px;
  color: rgb(207, 197, 197);
  background-color: #111;
}
.u-height {
  .loader {
    display: flex;
    justify-content: center;
    align-items: center;
    // padding: 50rem;
    height: 100vh;
    .loading {
      width: 20rem;
      img {
        display: block;
      }
    }
  }
  .root {
    background-color: #fff;
    min-height: 100%;
    .pylst {
      position: relative;
      overflow: hidden;
      display: block;
      span.back{
        position: absolute;
