        left: 5rem;
        top: 5rem;
        font-weight: 600;
      }
      .pylst_header {
        padding: 40rem 10rem 10rem 15rem;
        display: flex;
        background-size: cover;
        background-position: center;
        position: relative;
        overflow: hidden;
        &::before {
          content: "";
          filter: blur(20rem);
          width: 100%;
          height: 100%;
          transform: scale(1.5);
          position: absolute;
          background: inherit;
          top: 0rem;
          left: 0rem;
          z-index: 0;
        }
        &::after {
          content: "";
          background-color: rgba(0, 0, 0, 0.25);
          width: 100%;
          height: 100%;
