<template>
  <div
    class="song-item"
    href="#"
    @click.prevent="
      $emit('music-play', song);
      $emit('music-play-list');
    "
    :style="{ color: song?.id === songId ? '#03c862' : '' }"
  >
    <div
      class="hot-cred"
      :style="{ color: song?.id === songId ? '#03c862' : '' }"
    >
      {{ (i + 1) }}
    </div>
      <!-- {{ i + 1 > 9 ? i + 1 : "0" + (i + 1) }} -->
    <div class="sgfr">
      <div class="sgchfl">
        <div class="f-bd">{{ song.name }}</div>
        <div
          class="f-thide"
          :style="{ color: song?.id === songId ? '#03c862' : '' }"
        >
          <!-- <i class="sghot" v-if="song.song.sqMusic !== null"></i> -->
          {{ song.song.album.artists["0"].name }} - {{ song.song.album.name }}
        </div>
      </div>
      <div class="sgchfr" v-if="song.id !== songId">
        <span class="u-hm"></span>
      </div>
      <div v-else class="sgchfr active" :class="{ playing: playing }">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    songId: [String, Number],
    playing: Boolean,
    i: Number,
  },
};
</script>

<style lang="less">
.song-item {
  text-decoration: none;
  color: rgb(223, 217, 217);
  padding-left: 10rem;
  display: flex;
  .hot-cred {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    width: 40rem;
    font-size: 17rem;
    display: flex;
    color: rgb(222, 219, 219);
    margin-left: -10rem;
  }
  .sgfr {
    flex: 1 1 auto;
    display: flex;
    position: relative;
    &:after {
      position: absolute;
      // z-index: 2;
      content: "";
      top: 0;
      left: 0;
      pointer-events: none;
      box-sizing: border-box;
      width: 200%;
      height: 200%;
      transform-origin: top left;
      border: 0 solid rgba(255, 255, 255, 0.1);
      border-bottom-width: 1rem;
      transform: scale(0.5);
    }
    .sgchfl {
      padding: 6rem 0;
      width: 0;
      -webkit-box-flex: 1;
      flex: 1 1 auto;
      .f-bd {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: normal;

        font-size: 17rem;
      }
      .f-thide {
        font-size: 12rem;
        color: #888;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: normal;
        i.sghot {
          display: inline-block;
          width: 12rem;
          height: 8rem;
          margin-right: 4rem;
          background: url("../assets/index_icon_2x.png") no-repeat;
          background-size: 166rem 97rem;
        }
      }
    }
    @keyframes playingrunning {
      from {
        transform: rotateX(15deg);
      }
      to {
        transform: rotateX(60deg);
      }
    }
    .sgchfr {
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      padding: 0 10rem;
      position: relative;

      span.u-hm {
        display: inline-block;
        width: 22rem;
        height: 22rem;
        background: url("../assets/index_icon_2x.png") no-repeat;
        background-position: -24rem 0;
        background-size: 166rem 97rem;
      }
      &.active {
        display: flex;
        justify-content: space-around;
        width: 22rem;
        height: 22rem;
        position: absolute;
        right: 10rem;
        top: 50%;
        padding: 0;
        transform: translateY(-50%);
        span.u-hm {
          background-image: none;
        }
        span {
          width: 11%;
          height: 100%;
          background:#03c862;
          animation: playingrunning 0.8s infinite alternate;
          animation-play-state: paused;
          transform-origin: bottom;
          &:nth-child(1) {
            animation-delay: 0s;
          }
          &:nth-child(2) {
            animation-delay: -0.2s;
          }
          &:nth-child(3) {
            animation-delay: -0.4s;
          }
          &:nth-child(4) {
            animation-delay: -0.6s;
          }
        }

        &.playing {
          span {
            animation-play-state: running;
          }
        }
      }
    }
  }
}
</style>>
