<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <div class="m-home">
      <!-- <h2>音乐电台</h2>
      <div class="sone-car">
        <div class="radio-list">
          <RadioStation
            v-for="radio in radiostation"
            :key="radio.id"
            :radio="radio"
          />
        </div>
      </div> -->
      <h2>歌单推荐</h2>
      <div class="sone-car">
        <div class="card-list">
          <HomeCard v-for="item in personalizeds" :key="item.id" :item="item" />
        </div>
      </div>
      <div class="billboard">
        <div class="title">
          <h2
            class="hot"
            :class="{ show: showtitle ==='hot' }"
            @click="showtitle ='hot' "
          >
            热歌榜
          </h2>
          <h2
            class="new"
            :class="{ show:showtitle ==='new' }"
            @click="showtitle ='new'"
          >
            新歌榜
          </h2>
          <h2
            class="eura"
            :class="{ show:showtitle ==='eura' }"
            @click="showtitle ='eura'"
          >
            欧美榜
          </h2>
        </div>
        <section v-if="showtitle ==='hot'">
          <div class="sone-item">
            <div class="song-list">
              <HotSong
                v-for="(hotsong, i) in hotsongs"
                :key="hotsong.id"
                :hotsong="hotsong"
                :i="i"
                @music-play="$emit('music-play', $event)"
                @music-play-list="$emit('music-play-list', hotsongs)"
                :songId="songId"
                :playing="playing"
              />
            </div>
          </div>
        </section>
        <section v-else-if="showtitle ==='new'">
          <div class="sone-item">
            <div class="song-list">
              <SongItem
                v-for="(song, i) in newsongs"
                :key="song.id"
                :song="song"
                :i="i"
                @music-play="$emit('music-play', $event)"
                @music-play-list="$emit('music-play-list', newsongs)"
                :songId="songId"
                :playing="playing"
              />
            </div>
          </div>
        </section>
        <section v-else>
          <div class="sone-item">
            <div class="song-list">
              <EuropeAmerica
                v-for="(song, i) in eurpamersong"
                :key="song.id"
                :song="song"
                :i="i"
                @music-play="$emit('music-play', $event)"
                @music-play-list="$emit('music-play-list', eurpamersong)"
                :songId="songId"
                :playing="playing"
              />
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import HomeCard from "@/components/HomeCard.vue";
import SongItem from "@/components/SongItem.vue";
import HotSong from "@/components/HotSong.vue";
import EuropeAmerica from "@/components/EuropeAmerica.vue";
// import RadioStation from "@/components/RadioStation.vue";

export default {
  // name: 'HomeView',
  components: {
    // HelloWorld
    HomeCard,
    SongItem,
    HotSong,
    EuropeAmerica,
    // RadioStation,
  },
  props: {
    songId: [String, Number],
    playing: Boolean,
  },
  data: function () {
    return {
      personalizeds: [],
      newsongs: [],
      hotsongs: [],
      eurpamersong:[],
      showtitle:"hot",
      // radiostation: [],
    };
  },
  created() {
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized", {
        params: { limit: 6 },
      }),
      this.axios.get("https://apis.netstart.cn/music/personalized/newsong"),
    ]).then(([p, n]) => {
      this.personalizeds = p.data.result;
      this.newsongs = n.data.result;
    });
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        // this.updateTime = res.data.playlist.updateTime;
        this.hotsongs = res.data.playlist.tracks.slice(0, 20);
      });
    // this.axios
    //   .get("https://apis.netstart.cn/music//dj/catelist")
    //   .then((res) => {
    //     // this.updateTime = res.data.playlist.updateTime;
    //     this.radiostation = res.data.categories.slice(0, 6);
    //     // this.radiostation = res.data.categories;
    //     // console.log(res);
    //   });
    this.axios
      .get("https://apis.netstart.cn/music/playlist/track/all?id=2809513713", {
        params: { limit: 20 },
      })
      .then((res) => {
        this.eurpamersong = res.data.songs; 
      });
  },
};
</script>
