<template>
  <a
    href=""
    class="hotsong"
    @click.prevent="
      $emit('music-play', hotsong);
      $emit('music-play-list');
    "
    :style="{ color: hotsong?.id === songId ? '#03c862' : '' }"
  >
    <div
      class="hot-cred"
      :style="{ color: hotsong?.id === songId ? '#03c862' : '' }"      
    >{{ (i + 1) }}
    </div>
      <!-- {{ i + 1 > 9 ? i + 1 : "0" + (i + 1) }} -->
    <div class="hotsong-item">
      <div class="hotsg">
        <div class="f-thid">{{ hotsong.name }}</div>
        <div
          class="f-info"
          :style="{ color: hotsong?.id === songId ? '#03c862' : '' }"
        >
          <!-- <i class="sgihot" v-if="hotsong.sq !== null"></i> -->
          {{ hotsong.ar["0"].name }} - {{ hotsong.al.name }}
        </div>
      </div>
      <div class="hotauto" v-if="hotsong.id !== songId">
        <span class="hots"></span>
      </div>
      <div v-else class="hotauto active" :class="{ playing: playing }">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
  </a>
</template>

<script>
export default {
  props: {
    hotsong: Object,
    i: Number,
    songId: [Number, String],
    playing: Boolean,
  },
};
</script>

<style lang="less">
a.hotsong {
  text-decoration: none;
  color: rgb(208, 204, 204);
  display: flex;
  padding-left: 10rem;
  background-color: #111111;
  .hot-cred {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    width: 40rem;
    font-size: 17rem;
    display: flex;
    color: rgb(231, 229, 229);
    margin-left: -10rem;
  }
  .hotsong-item {
    -webkit-box-flex: 1;
    flex: 1 1 auto;
    display: flex;
    position: relative;
    &:after {
      position: absolute;
      // z-index: 2;
      content: "";
      top: 0;
      left: 0;
      pointer-events: none;
      box-sizing: border-box;
      width: 200%;
      height: 200%;
      transform-origin: top left;
      border: 0 solid rgba(255, 255, 255, 0.1);
      border-bottom-width: 1rem;
      transform: scale(0.5);
    }
    .hotsg {
      -webkit-box-flex: 1;
      flex: 1 1 auto;
      padding: 6rem 0;
      width: 0;
      .f-thid {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: normal;
        font-size: 17rem;
      }
      .f-info {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: normal;
        font-size: 12rem;
        color: #888;
        i.sgihot {
          display: inline-block;
          width: 12rem;
          height: 8rem;
          margin-right: 4rem;
          background: url("../assets/index_icon_2x.png") no-repeat;
          background-size: 166rem 97rem;
        }
      }
    }
    @keyframes playingrunning {
      from {
        transform: rotateX(15deg);
      }
      to {
        transform: rotateX(60deg);
      }
    }
    .hotauto {
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      padding: 0 10rem;
      position: relative;
      span.hots {
        display: inline-block;
        width: 22rem;
        height: 22rem;
        background: url("../assets/index_icon_2x.png") no-repeat;
        background-position: -24rem 0;
        background-size: 166rem 97rem;
      }
      &.active {
        display: flex;
        justify-content: space-around;
        width: 22rem;
        height: 22rem;
        position: absolute;
        right: 10rem;
        top: 50%;
        padding: 0;
        transform: translateY(-50%);
        span.u-hm {
          background-image: none;
        }
        span {
          width: 11%;
          height: 100%;
          background: #03c862;
          animation: playingrunning 0.8s infinite alternate;
          animation-play-state: paused;
          transform-origin: bottom;
          &:nth-child(1) {
            animation-delay: 0s;
          }
          &:nth-child(2) {
            animation-delay: -0.2s;
          }
          &:nth-child(3) {
            animation-delay: -0.4s;
          }
          &:nth-child(4) {
            animation-delay: -0.6s;
          }
        }

        &.playing {
          span {
            animation-play-state: running;
          }
        }
      }
    }
  }
}
</style>>
